'use client';

import { useAuth } from "@/context/auth-context";
import AttendanceMarker from "@/components/faculty/attendance-marker";
import AttendanceOverview from "@/components/student/attendance-overview";

export default function AttendancePage() {
  const { role } = useAuth();
  
  if (!role) return null;

  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold">Attendance</h1>
        <p className="text-muted-foreground">
          {role === 'student' 
            ? 'Review your attendance records.' 
            : 'Mark and manage student attendance.'}
        </p>
      </div>
      {role === 'student' ? <AttendanceOverview /> : <AttendanceMarker />}
    </div>
  );
}
