'use client';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { attendance, subjects } from "@/lib/mock-data";

export default function AttendanceOverview() {
    const totalAttended = attendance.reduce((acc, record) => acc + record.attendedClasses, 0);
    const totalClasses = attendance.reduce((acc, record) => acc + record.totalClasses, 0);
    const overallPercentage = totalClasses > 0 ? (totalAttended / totalClasses) * 100 : 0;

  return (
    <div className="flex flex-col gap-6">
      <Card>
        <CardHeader>
          <CardTitle>Overall Attendance</CardTitle>
          <CardDescription>Your aggregate attendance percentage across all subjects.</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex items-center gap-4">
            <span className="text-3xl font-bold text-primary">{overallPercentage.toFixed(1)}%</span>
            <Progress value={overallPercentage} className="h-3 flex-1" />
          </div>
        </CardContent>
      </Card>
      
      <div>
        <h2 className="text-2xl font-bold mb-4">Subject-wise Breakdown</h2>
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
            {attendance.map(record => {
            const subject = subjects.find(s => s.id === record.subjectId);
            const percentage = (record.attendedClasses / record.totalClasses) * 100;
            return (
                <Card key={record.subjectId} className="hover:shadow-md transition-shadow">
                <CardHeader className="pb-2">
                    <CardTitle className="text-lg" style={{ color: subject?.color }}>{subject?.name}</CardTitle>
                </CardHeader>
                <CardContent>
                    <Progress value={percentage} indicatorColor={subject?.color} />
                    <div className="flex justify-between mt-2 text-sm text-muted-foreground">
                    <span className="font-semibold">{percentage.toFixed(1)}%</span>
                    <span>{record.attendedClasses} / {record.totalClasses} classes</span>
                    </div>
                </CardContent>
                </Card>
            );
            })}
        </div>
      </div>
    </div>
  );
}

// A small extension to the Progress component to allow for dynamic colors
const OriginalProgress = Progress;
const NewProgress = ({ indicatorColor, ...props }: React.ComponentProps<typeof Progress> & { indicatorColor?: string }) => (
    <OriginalProgress 
        {...props} 
        indicatorClassName="transition-all"
        style={{
            // @ts-ignore
            '--indicator-color': indicatorColor,
        }}
    />
);

Object.assign(Progress, NewProgress)
if (Progress && typeof Progress === 'object') {
    (Progress as any).defaultProps = {
        ...((Progress as any).defaultProps || {}),
        indicatorClassName: "bg-[--indicator-color,hsl(var(--primary))]"
    };
}
