'use client';

import { useState } from 'react';
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Checkbox } from "@/components/ui/checkbox";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { timetable, subjects, students } from '@/lib/mock-data';
import { useToast } from '@/hooks/use-toast';
import { Label } from '../ui/label';

export default function AttendanceMarker() {
    const [selectedClass, setSelectedClass] = useState<string | undefined>();
    const { toast } = useToast();

    const facultyClasses = [...new Set(timetable.map(t => t.subjectId))].map(id => subjects.find(s => s.id === id));
    
    const handleSubmitAttendance = () => {
        if (!selectedClass) return;
        const subject = subjects.find(s => s.id === selectedClass);
        toast({
            title: "Attendance Submitted",
            description: `Attendance for ${subject?.name} has been successfully recorded.`
        });
        setSelectedClass(undefined);
    }

    return (
        <div className="flex flex-col gap-6">
            <Card>
                <CardHeader>
                    <CardTitle>Select a Class</CardTitle>
                    <CardDescription>Choose the subject and date to mark attendance for.</CardDescription>
                </CardHeader>
                <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                     <div className="space-y-2">
                        <Label htmlFor="subject-select">Subject</Label>
                        <Select onValueChange={setSelectedClass} value={selectedClass}>
                            <SelectTrigger id="subject-select" className="w-full">
                                <SelectValue placeholder="Select a subject" />
                            </SelectTrigger>
                            <SelectContent>
                                {facultyClasses.map(c => c && <SelectItem key={c.id} value={c.id}>{c.name}</SelectItem>)}
                            </SelectContent>
                        </Select>
                    </div>
                     <div className="space-y-2">
                        <Label htmlFor="date-select">Date</Label>
                        <input id="date-select" type="date" defaultValue={new Date().toISOString().split('T')[0]} className="flex h-10 w-full rounded-md border border-input bg-transparent px-3 py-2 text-sm ring-offset-background file:border-0 file:bg-transparent file:text-sm file:font-medium placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50" />
                     </div>
                </CardContent>
            </Card>

            {selectedClass && (
                 <Card className="animate-in fade-in duration-500">
                    <CardHeader>
                        <CardTitle>Student List for {subjects.find(s => s.id === selectedClass)?.name}</CardTitle>
                        <CardDescription>Mark present for each student. All are present by default.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <div className="border rounded-md">
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead className="w-[100px]">Roll No.</TableHead>
                                    <TableHead>Student Name</TableHead>
                                    <TableHead className="text-right w-[100px]">Present</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {students.map(student => (
                                    <TableRow key={student.id}>
                                        <TableCell>{student.rollNumber}</TableCell>
                                        <TableCell className="font-medium">{student.name}</TableCell>
                                        <TableCell className="text-right">
                                            <Checkbox defaultChecked id={`attendance-${student.id}`} />
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                        </div>
                         <div className="mt-6 flex justify-end">
                            <Button onClick={handleSubmitAttendance}>Submit Attendance</Button>
                        </div>
                    </CardContent>
                 </Card>
            )}
        </div>
    );
}
